@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@600;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Spartan', sans-serif;
}

body {
  background-color: #e7312b;
}
/*# sourceMappingURL=App.css.map */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.checkoutCart {
  background-color: #fff;
  width: 20%;
  min-height: 350px;
  right: 0;
  border-radius: 5px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  position: absolute;
  display: none;
}

.checkoutCart h1,
.checkoutCart p {
  background-color: #fde74c;
  text-align: center;
}

.checkoutCart h4 {
  padding: 10px;
}

.checkoutCart h4 button {
  background-color: #e7312b;
  border: 0;
  padding: 2% 10%;
  border-radius: 5px;
  cursor: pointer;
}

.checkoutCart button {
  background-color: #fde74c;
  border: 0;
  padding: 2% 10%;
  border-radius: 5px;
  margin: 0 20%;
  cursor: pointer;
}

.nav {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 100px;
  background-color: #e7312b;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.nav li {
  text-decoration: none;
  list-style: none;
  color: #fff;
  margin: 0 10px;
  padding: 10px;
  cursor: pointer;
  font-weight: 600;
}

.nav a {
  text-decoration: none;
  list-style: none;
  color: #fff;
}

.nav li:hover {
  color: #1b1b1b;
}

.nav img {
  width: 200px;
  height: 200px;
  margin: 0 7%;
}

.nav .left {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  flex: 1 1;
}

.nav .right {
  display: flex;
  justify-content: flex-end;
  flex-direction: row;
}

.nav .right .cart {
  background-color: #fde74c;
  border-bottom: 0;
  border-top: 0;
  border-radius: 50px;
  color: #1b1b1b;
  transition: 0.1s;
}

.nav .right .cart:hover {
  border: 3px solid #fff;
  color: #e7312b;
}

.mobileNav,
.itemsMobile {
  display: none;
}

@media screen and (max-width: 665px) {
  .checkoutCart {
    background-color: #fff;
    width: 100%;
    min-height: 350px;
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    display: none;
  }
  .checkoutCart h1,
  .checkoutCart p {
    background-color: #fde74c;
    text-align: center;
  }
  .checkoutCart h4 {
    padding: 10px;
  }
  .checkoutCart .checkoutBtn {
    margin: 0 30%;
  }
  .nav {
    display: none;
  }
  .mobileNav {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 100px;
    max-height: -webkit-max-content;
    max-height: -moz-max-content;
    max-height: max-content;
    justify-content: space-between;
    background-color: #e7312b;
  }
  .mobileNav li {
    text-decoration: none;
    list-style: none;
    cursor: pointer;
    margin: 0 10%;
  }
  .mobileNav li:hover {
    color: #1b1b1b;
  }
  .mobileNav .mobilelogo {
    width: 150px;
    height: 150px;
  }
  .mobileNav .menuBtn {
    background-color: #fde74c;
    border-radius: 10%;
    border-bottom: 0;
    border-top: 0;
    padding: 10px;
  }
  .itemsMobile {
    list-style: none;
    text-decoration: none;
    background-color: #e7312b;
    border-bottom: 2px solid rgba(255, 255, 255, 0.2);
    color: #fff;
    display: none;
    flex-direction: column;
    justify-content: center;
  }
  .itemsMobile li {
    margin: 10px auto;
    color: #fff;
    font-size: 17px;
    padding: 10px;
    cursor: pointer;
  }
  .itemsMobile a {
    text-decoration: none;
    list-style: none;
    color: #fff;
  }
  .itemsMobile a:hover,
  .itemsMobile li:hover {
    color: #1b1b1b;
  }
  .itemsMobile .cart {
    background-color: #fde74c;
    border-bottom: 0;
    border-top: 0;
    border-radius: 50px;
    color: #1b1b1b;
  }
  .itemsMobile .cart:hover {
    border: 3px solid #fff;
    color: #e7312b;
  }
}
/*# sourceMappingURL=Nav.css.map */
.Header {
  margin: 5% 0;
  width: 100%;
  min-height: 400px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  color: #fff;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.Header div {
  font-size: 30px;
  width: 50%;
}

.Header div button {
  background-color: #fde74c;
  border-radius: 30px;
  border: 0;
  border-top: 0;
  border-bottom: 0;
  padding: 3%;
  transition: 0.1s;
  cursor: pointer;
}

.Header div button:hover {
  color: #e7312b;
  border: 5px solid #fff;
}

.Header img {
  width: 350px;
  height: 350px;
}

@media screen and (max-width: 660px) {
  .Header {
    flex-direction: column;
  }
  .Header div {
    font-size: 20px;
  }
  .Header img {
    width: 250px;
    height: 250px;
  }
}
/*# sourceMappingURL=Header.css.map */
.Info {
  background-color: #fff;
  width: 100%;
  min-height: 400px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  padding: 4%;
  color: #1b1b1b;
}

.Info h1 {
  text-align: center;
}

.Info h1 span {
  color: #e7312b;
}

.Info i {
  font-size: 12px;
  color: grey;
}

.Info .how {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}

.Info .steps {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.Info .steps span {
  margin: 20px;
  background-color: #e7312b;
  padding: 20px;
  border-radius: 50%;
  color: #fff;
}

@media screen and (max-width: 690px) {
  .Info .steps {
    display: flex;
    flex-direction: column-reverse;
  }
}
/*# sourceMappingURL=OrderInfo.css.map */
.Menu {
  background-color: whitesmoke;
  width: 100%;
  min-height: 500px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
}

.Menu h1 {
  padding: 50px 0;
  text-align: center;
  color: #e7312b;
}

.Menu h1 span {
  color: #1b1b1b;
}

.Menu .pizzas {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.Menu .pizzas .pizza {
  background-color: #fff;
  border-radius: 5px;
  margin: 10px;
  width: 35%;
  color: #353535;
  text-align: center;
}

.Menu .pizzas .pizza img {
  width: 100%;
  border-radius: 5px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.Menu .pizzas .pizza span {
  color: #e7312b;
}

.Menu .pizzas .pizza button {
  margin: 10px 0;
  background-color: #fde74c;
  border-bottom: 0;
  border-top: 0;
  border: 0;
  border-radius: 50px;
  padding: 5%;
  font-weight: 900;
  transition: 0.1s;
  color: #353535;
  cursor: pointer;
}

.Menu .pizzas .pizza button:hover {
  color: #e7312b;
  border: 4px solid #fff;
}

.Menu .pizzas .pizza:hover {
  background-color: #e7312b;
  color: #fff;
}

.Menu .pizzas .pizza:hover span {
  color: #fff;
}
/*# sourceMappingURL=Menu.css.map */
.Newsletter {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  background-color: #e7312b;
  width: 100%;
  min-height: 250px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
}

.Newsletter h1 {
  color: #fff;
  width: 40%;
  margin: 6% 0;
}

.Newsletter h1 span {
  color: #fde74c;
}

.Newsletter form {
  font-weight: 800;
}

.Newsletter form input {
  background-color: #fff;
  border: 0;
  padding: 5% 10%;
  border-radius: 4px;
  outline: none;
}

.Newsletter form button {
  margin: 5px;
  background-color: #fde74c;
  border-bottom: 0;
  border-top: 0;
  border: 0;
  border-radius: 30px;
  padding: 3%;
  cursor: pointer;
}

.Newsletter form button:hover {
  border: 4px solid #fff;
  color: #e7312b;
}

@media screen and (max-width: 690px) {
  .right {
    display: none;
  }
}
/*# sourceMappingURL=Newsletter.css.map */
.Footer {
  background-color: #1b1b1b;
  width: 100%;
  min-height: 300px;
  max-height: -webkit-max-content;
  max-height: -moz-max-content;
  max-height: max-content;
  color: #fff;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.Footer img {
  width: 50%;
}

.Footer p {
  font-weight: 600;
  font-size: 12px;
  margin: 20px 0;
}

@media screen and (max-width: 690px) {
  .Footer img {
    width: 100%;
  }
}
